import axios from 'axios';
import { HOSTNAME, HTTP_CONFIG } from './config';
import $ from 'jquery';

export const isAuthenticated = () => {
    const status = (localStorage.getItem('token') !== null) && window.db.get('expire');
    if(!status) logout();
    return status;
};

export const resetPassword = ({ enc, password, url },callback) => {
  // const $api = `https://adms-account-charlie.ibid.astra.co.id/MAP/users/ResetPassword/apa`;
  const $api = "http://localhost/Ibid_ADMS_ServiceAccount/index.php/MAP/users/ResetPassword/apa";
  const $body = {
    enc: enc,
    password: password,
    url: url
  };
  console.log($body,'=====HTTP_CONFIG===');

  // $.ajax({
  //   method: "POST",
  //   contentType: "application/json", // this
  //   dataType: "json", // and this
  //   headers: {
  //       "Access-Control-Allow-Origin": "*",
  //   },
  //   url: $api,
  //   data: $body
  // })
  //   .done(function( msg ) {
  //     alert( "Data Saved: " + msg );
  // });

  axios.post($api, $body, HTTP_CONFIG)
  .then(response => {
    const { data } = response;
    if(!data.ok){
      callback({ status: false, message: data.message });
    } else {
      callback({ status: true, message: 'success' });
    }
  }).catch(err => {
      console.log(err);
      callback({ status: false, message: 'Server Error' });
  })
}

export const authenticate = ({ username, password, remember }, callback) => {
    const $api = `${HOSTNAME}/api/users/authenticate`;
    const $body = {
        username: username,
        password: password
    };
    axios.post($api, $body, HTTP_CONFIG)
    .then(response => {
        const { data } = response;
        if (!data.ok) {
            callback({ status: false, message: data.msg });
        } else {
            login(data, remember, password);
            callback({ status: true, message: 'sucess' });
        }
    }).catch(err => {
        console.log(err);
        callback({ status: false, message: 'Server Error' });
    } )
}

export const logout = () => {
    axios.defaults.headers.common['Authorization'] = '';
    // localStorage.clear();
    localStorage.removeItem('token');
    localStorage.removeItem('id');
    localStorage.removeItem('username');
    localStorage.removeItem('email');
    localStorage.removeItem('company');
    localStorage.removeItem('subsType');
    localStorage.removeItem('subsDate');
    localStorage.removeItem('subsTime');
}

export const login = ({
    token,
    user: {
        UserId = 0,
        Name = "user.name",
        username = "user.email",
        subsType = "Regular",
        EndDate = "0000-00-00",
        sisaHari = " - ",
        Company = "Personal User"
    }
}, remember = false, password) => {
    // eslint-disable-next-line
    window.db.set("expire", true, 86400000);
    localStorage.setItem('token', token);
    localStorage.setItem('id', UserId);
    localStorage.setItem('username', Name);
    localStorage.setItem('email', username);
    localStorage.setItem('company', Company);
    localStorage.setItem('subsType', subsType);
    localStorage.setItem('subsDate', EndDate);
    localStorage.setItem('subsTime', sisaHari);

    if(remember) {
        localStorage.setItem('saved_username', username);
        localStorage.setItem('saved_password', password);
    } else {
        localStorage.removeItem('saved_username');
        localStorage.removeItem('saved_password');
    }
}
