import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { toggleSidebar } from 'actions/dashboard.action';

class SidebarComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }

    MenuItem(param, i) {
        return (
            <NavLink key={'menu_item_'+i} to={`/dashboard/${param.path}`} onClick={this.props.toggleSidebar}>
                <div className={"menu-item " + (param.active ? "active" : "")} onClick={() => this.props.onClick(param.path)}>
                    { param.icon }
                    <div className="title">{param.title}</div>
                </div>
            </NavLink>
        );
    }

    // Action to navigate

    render() {
        let menuItems =[];
        for(let i = 0; i < this.props.option.length; i++){
            menuItems.push(this.MenuItem(this.props.option[i], i));
        }

        return (
            <div className="sidebar-container">
                <div className="menu">
                    { menuItems }
                </div>
            </div>
        )
    }
}


const mapStateToProps = state => ({
    ...state.dashboardReducer
})

const mapDispatchToProps = dispatch => ({
    toggleSidebar: () => dispatch(toggleSidebar())
})

export default connect(mapStateToProps, mapDispatchToProps)(SidebarComponent);