import axios from 'axios';
import { HOSTNAME, HTTP_CONFIG } from 'services';

export const createProfile = (callback) => {
    const api = `${HOSTNAME}/api/profiles/${localStorage.getItem('id')}`;
    const body = {
        "key": "default",
        "value": "[[],[]]"
    }
    axios.post(api, body, HTTP_CONFIG)
    .then(({data, ...response}) => {
        callback(true);
    }).catch(e => console.log(e));

}

export const readProfile = (callback) => {
    const api = `${HOSTNAME}/api/profiles/${localStorage.getItem('id')}/default`;
    axios.get(api, HTTP_CONFIG)
    .then(({data, ...response}) => {

        const rawWidgets = data.length > 0 ? data[0].value : null ;
        const widgets = rawWidgets !== null ? JSON.parse(rawWidgets) : [];
        callback(widgets);
    }).catch(e => console.log(e));
}

export const updateProfile = (parameter, callback) => {
    const api = `${HOSTNAME}/api/profiles/${localStorage.getItem('id')}`;
    const body = {
        "key": "default",
        "value": JSON.stringify(parameter)
    }
    axios.patch(api, body, HTTP_CONFIG)
    .then(({data, ...response}) => {
        callback(true);
    }).catch(e => console.log(e));
}


export const requestData = (paramter, callback) => {
    const api = `${HOSTNAME}/api/insight`;
    axios.post(api, paramter, HTTP_CONFIG)
    .then(({data, ...response}) => {
        // const series = data
        console.log(data,'---data----');
        callback(data.data);
    }).catch(e => console.log(e));
}
