import React, { Component } from 'react';
import axios from 'axios';
import { IoMdHome, IoMdPricetag, IoIosStats} from 'react-icons/io';
import { Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from 'services';

import {
    HeaderComponent,
    SidebarComponent,
    NoticeCardComponent
} from './components';

import {
    OverviewScreen,
    InsightsScreen,
    MarketPriceDBScreen,
    DetailScreen,
    NotFound,
    TeasersScreen
} from './screens';

import UserProfile from './components/user-profile.component';

class Dashboard extends Component {
    constructor(props) {
        super(props);
        //determine if user is on demo account or not
        let options;
        if (localStorage.getItem('email') === 'userdemo041@gmail.com') {
          //if in demo account
          options = [
              { 'title': 'Overview', 'path': 'overview', 'icon': <IoMdHome className='icon' />, 'component': OverviewScreen, active: false},
          ];
        }else {
          //if in non-demo account
          options = [
              { 'title': 'Overview', 'path': 'overview', 'icon': <IoMdHome className='icon' />, 'component': OverviewScreen, active: false},
              { 'title': 'Insights', 'path': 'insights', 'icon': <IoIosStats className='icon' />, 'component': InsightsScreen, active: false },
              { 'title': 'Market Price DB', 'path': 'marketplace_db', 'icon': <IoMdPricetag className='icon' />, 'component': MarketPriceDBScreen, active: false },
          ];
        }
        this.state = {
            'isNoticed': localStorage.getItem('subsTime') <= 7,
            'redirect': undefined,
            'option': options,
        };
    }

    componentDidMount() {
        let screen = this.props.match.params.screen;
        // Set the selected menu in options
        this.changePage(screen || 'overview');
    }

    componentWillMount(){
        let path = this.props.location.pathname;
        let history = this.props.history;
        axios.defaults.headers.common['Authorization'] = `bearer ${localStorage.getItem('token')}`;
        axios.interceptors.response.use((response) => {
            return response;
        }, function (error) {
            // Do something with response error
            if (error.response.status === 401) {
                history.push('/login');
                console.log('unauthorized, logging out ...');
                logout();
            }
            return Promise.reject(error.response);
        });
        // Check the path and Redirect if Necessary
        if (path === '/dashboard' || path === '/dashboard/') {
            history.push('/dashboard/overview');
        }
    }

    changePage(target) {
        let option = this.state.option;

        // Find the index of active option
        const activeIndex = option.indexOf(option.find(x => x.active === true));
        // Deactivating the active option if exist
        if (typeof activeIndex !== 'undefined' && activeIndex >= 0) { option[activeIndex].active = false; }

        //Activating new option
        const targetIndex = option.indexOf(option.find(x => x.path === target));
        // Activating the target option if exist
        if (typeof targetIndex !== 'undefined' && targetIndex >= 0) { option[targetIndex].active = true; }

        this.setState({'option': option});
    }

    render() {
        const { isSidebarActive } = this.props
        return (
            <div className="structure">
                <UserProfile />
                <div className="header">
                    <HeaderComponent onClickHome={(path) => this.changePage(path)} />
                </div>
                <div className={"sidebar" + (isSidebarActive ? ' active' : '' )}>
                    <SidebarComponent option={this.state.option} onClick={(path) => this.changePage(path)} />
                </div>
                <div className="body">
                    {/* Notice */}
                    { this.state.isNoticed && <NoticeCardComponent action={e => {
                        this.setState({isNoticed: false});
                    }} />}

                    <Switch>
                        <Route path='/dashboard/overview' component={OverviewScreen} exact />
                        <Route path='/dashboard/insights' component={InsightsScreen} exact />
                        <Route path='/dashboard/marketplace_db' component={MarketPriceDBScreen} exact />
                        <Route path='/dashboard/detail' component={DetailScreen} exact />
                        <Route path='/dashboard/detail/:id' component={DetailScreen} exact />
                        <Route path='/dashboard/teaser' component={TeasersScreen} exact />
                        <Route component={NotFound} exact />
                    </Switch>
                </div>
            </div>
        );
    }
}


const mapStateToProps = state => ({
    ...state.dashboardReducer
})

const mapDispatchToProps = dispatch => ({
    // simpleAction: () => dispatch(simpleAction())
})

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
